.section-heading{
    width: 100% !important;
    text-align: center !important;
}
.section-column{
    display: flex;
    justify-content: space-between;
    margin-left: -30px;
    font-size: 15.5px;
}
.section-column div img{
    width: 90px;
    height: 90px;
}

.section-row{
    margin-left: -6vw !important;
}
.column{
    margin-left: -12px;
    padding-left: -20px !important;
    border-right: 1px solid lightgray !important;
}


@media(max-width:1000px){
   
   .section-column div img{
       width: 70px;
       height: 70px;
   }
   .section-column{
       margin-left: -20px;
       font-size: 15px;
   }
}

@media(max-width:992px){
    .hide{
        display: none;
    }
}

@media(max-width:900px){
    .logo-row{
        margin-left: 1em !important;
    }
    .section-column{
        display: block !important;
        width: 100%;
        max-width: 500px;
        margin: auto;
        font-size: 17px;
        word-spacing: 5px;
        
    }
    .section-column div{
        margin-top: 20px;
    }
    .section-column div img{
        width: 50px;
        height: 50px;
    }
    .column{
        border-right: none !important;
        margin: 0px;
    }
}

@media(max-width:768px){
    .logo-1{
        width: 130px;
    }
    .logo-2{
        width: 120px;
        margin-left: -1em;
    }
    .logo-3{
        width: 60px;
        margin-left: -2.5em;
    }
    .logo-4{
        width: 140px;
        margin-left: -2.5em;
    }
}

@media(max-width:600px){
    .logo-1{
        width: 110px;
        height: 70px;
    }
    .logo-2{
        width: 90px;
        height: 40px;
        margin-top: -20px;
        margin-left: -1.1em;
    }
    .logo-3{
        width: 30px;
        height: 30px;
        margin-top: -2px;
        margin-left: -2.2em;
    }
    .logo-4{
        width: 100px;
        height: 80px;
        margin-top: -5px;
        margin-left: -2em;
    }
}

@media(max-width:470px){
    .logo-1{
        width: 90px;
        height: 70px;
        margin-left: -0.5em;

    }
    .logo-2{
        width: 70px;
        height: 35px;
        margin-top: -20px;
        margin-left: -2.6em;
    }
    .logo-3{
        width: 30px;
        height: 30px;
        margin-top: -2px;
        margin-left: -3em;
    }
    .logo-4{
        width: 90px;
        height: 70px;
        margin-top: -2px;
        margin-left: -1em;
    }
}